.tags {
    border-radius: 10px;
    text-align: center;
    padding: 5px 40px;
    margin: 10px 10px 10px 0;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, .2);
    @include tags_desktop;
}

@media only screen and (max-width: 500px) {
    .tags {
        @include tags_mobile;
        padding: 5px 20px;
    }
}